@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&subset=cyrillic);
@import url(https://weloveiconfonts.com/api/?family=fontawesome);
@import url(https://meyerweb.com/eric/tools/css/reset/reset.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "utf-8";
/* CSS Document */

/* ---------- FONTAWESOME ---------- */

[class*="fontawesome-"]:before {
  font-family: 'FontAwesome', sans-serif;
}

/* ---------- GENERAL ---------- */

body {
	background-color: #fff;
	color: #606468;
	font-family: 'Roboto', sans-serif;
	font-size: 14px;
  line-height: 1.5em;
}

.app {
	margin: 0 auto;
	max-width: 964px;
}

a {
	color: #666666;
	text-decoration: none;
}

a:hover {
	color: #61dafb;
}

input {
	border: none;
	font-family: 'Open Sans', Arial, sans-serif;
	font-size: 14px;
	line-height: 1.5em;
	padding: 0;
	-webkit-appearance: none;
}

p {
	line-height: 1.5em;
}

.clearfix { *zoom: 1; }  /* For IE 6/7 */
.clearfix:before, .clearfix:after {
	display: table;
	content: "";
}
.clearfix:after { clear: both; }




/* ---------- SIGN IN & SIGN UP FORMS ---------- */

.user-form-size {
	margin: 50px auto;
	width: 280px;
}

.user-form form span {
	background-color: #d9d9d9;
	border-radius: 3px 0px 0px 3px;
	-moz-border-radius: 3px 0px 0px 3px;
	-webkit-border-radius: 3px 0px 0px 3px;
	color: #000000;
	display: block;
	float: left;
	height: 50px;
	line-height: 50px;
	text-align: center;
	width: 50px;
}

.user-form form input {
	height: 50px;
	outline:none;
}

.user-form form input[type="text"], input[type="password"] {
	background-color: #e6e6e6;
	border-radius: 0px 3px 3px 0px;
	-moz-border-radius: 0px 3px 3px 0px;
	-webkit-border-radius: 0px 3px 3px 0px;
	color: #000000;
	font-size: 16px;
	margin-bottom: 1em;
	padding: 0 16px;
	width: 198px;
}

.user-form form input:focus {
	color:#000000;
}

.user-form > p {
	text-align: center;
}

.user-form > p span {
	padding-left: 5px;
}

.formErrors p {
  margin: -5px 0 5px 0;
}

/* ---------- BUTTONS ---------- */
.button {
	border: 2px solid #61dafb;
	border-radius: 3px;
	-moz-border-radius: 3px;
	-webkit-border-radius: 3px;
	background-color: #61dafb;
	color: #fff;
	font-weight: bold;
	margin-bottom: 2em;
	text-transform: uppercase;
	cursor:pointer;
}

.button:hover {
	background-color: #fff;
	color: #61dafb;
}

.button-form  {
	width: 280px;
}

.button-add {
	width: 100px;
}



/* ---------- HEADER ---------- */
header {
	display: flex;
	justify-content: space-between;
	margin: 5px;
	padding: 20px;
}

header h1 {
	font-size: calc(12px + 3vmin);
	font-weight: bold;
	letter-spacing: 1px;
	line-height: 1;
	text-align: center;
}

header h1 a {
	color: #61dafb;
}

header nav {
	text-align: right;
}

header nav li {
	margin: 5px 0;
}



/* ---------- MAIN ---------- */
main {
  min-height: 65vh;
}

.home {
	text-align: center;
	width: 90%;
	margin: 0 auto;
}

.home h2 {
	text-align: center;
	font-size: 20px;
	font-weight: bold;
	margin: 20px 0;
}



/* ---------- FOOTER ---------- */
footer {
	display: flex;
	justify-content: space-evenly;
	min-height: 10vh;
	margin-top: 20px;
}


/* ---------- MEDIA SCREEN -- MOBILE ---------- */
@media (max-width: 600px){
	.user-form form input[type="text"], input[type="password"] {
		font-size: 16px;
	}
	.home {
		width: 90%;
	}
}

/* ---------- MEDIA SCREEN -- TABLET ---------- */
@media screen and (min-width: 601px) {
	header h1 {
		margin-left: 20px;
	}
	.home {
		width: 80%;
	}
}

/* ---------- MEDIA SCREEN -- DESKTOP ---------- */
@media screen and (min-width: 965px) {
	header h1 {
		margin-left: 20px;
	}
	.home {
		width: 70%;
	}
}
/* ---------- MEDIA SCREEN -- MOBILE ---------- */
@media (max-width: 600px){
    .task-manager {
        width: 100%;
    }
    .task-manager__item {
        padding: 12px 0;
    }
    .task-manager__checkbox,
    .task-manager__checkbox {
        margin-left: 15px;
    }
    .task-manager__priority,
    .task-manager__users {
        display: none;
    }
    .task-manager__deadline {
        width: 80px;
    }
    .task-manager__status {
        width: 100px;
    }
}


/* ---------- MEDIA SCREEN -- TABLET ---------- */
@media screen and (min-width: 601px){
    .task-manager {
        min-width: 600px;
        max-width: 700px;
    }
    .task-manager__item {
        padding: 12px 0;
    }
    .task-manager__deadline {
        width: 100px;
    }
    .task-manager__status {
        width: 100px;
    }
}


/* ---------- MEDIA SCREEN -- DESKTOP ---------- */
@media screen and (min-width: 965px) {
    .task-manager {
        width: 700px;
    }
    .task-manager__item {
        padding: 8px 0;
    }
    .task-manager__deadline {
        width: 120px;
    }
    .task-manager__status {
        width: 120px;
    }
}



.task-manager {
    margin: 0 auto;
    background-color: white;
}

.task-manager__control-panel {
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

.task-manager__header {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #999999;
    padding: 5px 0;
    font-weight: bold;
}

.task-manager__list {
	color: #666666;
	margin: 0 auto;
}

.task-manager__item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #999999;
}

.task-manager__checkbox,
.task-manager__checkbox {
    width: 30px;
    margin-right: 10px;
	-webkit-appearance: checkbox;
}

.task-manager__name {
    flex: 2 1;
}

.task-manager__priority {
	width: 60px;
}

.task-manager__priority,
.task-manager__deadline,
.task-manager__users,
.task-manager__status {
    text-align: center;
}

.task-manager__users {
    height: 21px;
	width: 130px;
}

.task-manager__avatar {
    width: 20px;
    margin: 0 3px;
}

.task-manager__checkbox:checked + .task-item {
    background-color: grey;
}

.task-manager__remove {
	width: 5%;
    text-align: right;
}

.task-manager__remove-button {
	font-size: 18px;
	background-color: #2c3338;
	border: 1px solid #fff;
	border-radius: 3px;
	color: #fff
}

.task-manager__remove-button:hover {
	background-color: #515861;
}

.control__button {
    font-size: 14px;
    background-color: #fff;
    color: #61dafb;
    border: 2px solid #61dafb;
    border-radius: 4px;
    margin: 10px;
    padding: 5px 10px;
  }
  
  .control__button:hover {
    background-color: #61dafb;
    color: #fff;
  }
  .control__button:active:focus { outline: none; }



/* ---------- BACKGROUND COLORS ---------- */
.Medium {
    background-color: #f6a435;
    color: #fff;
}

.High {
    background-color: #3779cc;
    color: #fff;
}

.Low {
    background-color: #81929d;
    color: #fff;
}


/* ---------- FONT COLORS ---------- */
.Stuck {
    color: #d23437;
}

.Work-on-it {
    color: #f27720;
}

.Done {
    color: #1fa227;
}

.New {
    color: #3678cc;
}
/* ---------- MEDIA SCREEN -- MOBILE ---------- */
@media (max-width: 600px){
  .modal__body {
    width: 100vw;
    height: 100vh;
  }
  .modal__input,
  .modal__email,
  .modal__textarea {
    width: 86vw;
    font-size: 16px;
  }
  .modal__email_users, 
  .modal__email_input {
    font-size: 16px;
  }
  .modal__select {
    width: 38vw;
    font-size: 16px;
  }
}

/* ---------- MEDIA SCREEN -- TABLET && DESKTOP ---------- */
@media (min-width: 601px){
  .modal {
    padding-top: 5rem;
  }
  .modal__body {
    width: 500px;
    height: 600px;
    border-radius: 5px;
  }
  .modal__input,
  .modal__email,
  .modal__textarea {
    width: 500px;
    font-size: 14px;
  }
  .modal__email_users, 
  .modal__email_input {
    font-size: 14px;
  }
  .modal__select {
    width: 200px;
    font-size: 14px;
  }
}



.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  overflow-y: scroll;
}

.modal__body {
  padding: 7vmin;
  background: #fff;
}

.modal__title {
  color: #61dafb;
  text-align: center;
  font-size: 18px;
}

.modal__email {
  display: flex;
  justify-self: start;
  flex-wrap: wrap;
}

.modal__email_checked {
  border: 1px solid #cccccc;
  margin: 2px 5px;
  padding: 0 5px;
}

.modal__email_remove {
  margin-left: 10px;
}

.modal__email_remove:hover {
  color: #a6a6a6;
}

.modal__email_users {
  height: 60px;
  overflow: scroll;
  border: 1px solid #ccc;
}

.modal__email_input{
  border: none;
  outline: none;
  margin-left: 5px;
}

.modal__form {
  margin: 20px 0;
}

.row__title {
  color: #666666;
  font-size: 18px;
  font-weight: bold;
  margin: 5px;
  width: 100%;
}

.modal__row {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 5px 0;
}

.modal__email,
.modal__input,
.modal__select,
.modal__textarea {
  box-sizing: border-box;
  outline: none;
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #81929d;
}

.modal__textarea {
  height: 100px;
}

.modal__controls {
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  margin: 10px auto;
}
/* ---------- MEDIA SCREEN -- MOBILE ---------- */
@media (max-width: 600px){
	.task__container {
		width: 90%;
    }
    .task__deadline,
    .task__users,
    .task__status {
        font-size: 16px;
    }
}

/* ---------- MEDIA SCREEN -- TABLET ---------- */
@media screen and (min-width: 601px) {
	.task__container {
		width: 80%;
    }
    .task__deadline,
    .task__users,
    .task__status {
        font-size: 14px;
    }
}

/* ---------- MEDIA SCREEN -- DESKTOP ---------- */
@media screen and (min-width: 965px) {
	.task__container{
		width: 70%;
    }
    .task__deadline,
    .task__users,
    .task__status {
        font-size: 14px;
    }
}




.task__container{
    margin: 0 auto;
}

.task__title {
    font-size: 18px;
    font-weight: bold;
}

.task__priority {
    font-size: 16px;
    font-weight: lighter;
    padding: 0 5px;
    margin: 0 10px;
}

.task__deadline,
.task__users,
.task__status {
    margin: 10px 0;
}

.task__deadline span,
.task__users span,
.task__status span {
    margin: 0 10px;
}

.task__users {
    display: flex;
    flex-wrap: wrap;
}

.task__controls {
    display: flex;
    justify-content: space-evenly;
    width: 80%;
    margin: 10px auto;
}
.user__container {
    width: 400px;
    margin: 0 auto;
    text-align: center;
}

.user__profile {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin: 20px 0;
}

.user_description {
    text-align: left;
    margin-top: 10px;
}

.user_description h3 {
    font-size: 18px;
    margin: 10px 0;
}

.user_description p {
    margin: 10px 0;
}

.user__avatar {
    width: 150px;
    height: 150px;
}

.user__count {
    font-size: 20px;
    font-weight: bold;
}
